.menu-panel {
  background-color: white;
  font-size: 24px;
  width: 400px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  -o-transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

.menu-panel#hide {
  -webkit-transform: translateX(-100vw);
      -ms-transform: translateX(-100vw);
          transform: translateX(-100vw);
}

.menu-panel#show {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.menu-panel-header {
  height: 56px;
  padding: 1em 0 1em 0;
}

.menu-panel-header #close-menu {
  margin: 0 5% 0 auto;
}

.menu-panel-content {
  overflow-x: hidden;
  overflow-y: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
}

.menu-panel-content-section {
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  padding: 1em 0 1em 0;
}

.menu-panel #menu-item:hover {
  color: black;
}

.menu-panel-share-ribbon {
  padding: 1em 0 1em 0;
  margin-bottom: 0;
  margin-top: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



