.monty-hall {
  background-color: white;
  width: 80%;
  height: 60%;
  margin: 20% 10% 20% 10%;
  z-index: 1;
  display: grid;
  grid-template-areas: "heading heading" "slider interactive";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 3rem 300px;
}

.monty-hall-heading {
  text-align: center;
  font-family: "Yellowtail", cursive;
  font-size: 2rem;
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  grid-area: heading;
  -ms-flex-item-align: center;
      align-self: center;
}

#monty-hall-slider {
  grid-area: slider;
}

.monty-hall-slider-description {
  overflow-y: auto;
  text-align: justify;
  font-size: 1rem;
  padding: 1em;
}

.monty-hall-interactive {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  -ms-flex-pack: center;
      justify-content: center;
  grid-area: interactive;
}

.monty-hall-interactive-gifts,
.monty-hall-interactive-instructions,
.stick-switch,
.tooltip {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#stick-button,
#switch-button {
  width: 168px;
  font-size: 14px;
  margin: 2.5%;
}

#tooltip-icon,
#tooltip-text {
  font-size: 1rem;
  white-space: pre;
  margin: 2.5%;
}

#gift-button {
  border-width: 1px;
  border-color: lightgray;
  border-style: solid;
  width: 72px;
  height: 72px;
  margin: 2.5%;
}

#gift-button:hover {
  background-color: whitesmoke;
}



